import React, {Component} from 'react';
import { Link } from 'react-router-dom';

export default class List extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lessonList: [],
    }
  }

  componentDidMount() {
    fetch('/json/menu.json').then((response) => {
      return response.json();
    }).then((data)=>{
      this.setState({
        lessonList:data
      })
    })
  }

  render() {
    const { lessonList } = this.state;
    return(
      <div>
        <ul>
          {
            lessonList.map((item,index)=>(
              <li key={index}><Link to={`/details/${item.url}`}>{item.title}</Link></li>
            ))
          }
        </ul>
      </div>
    )
  }
}