import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);
    this.audioEl = [];
    this.state = {
      theCurrentBroadcast:null,
      content:{
        list:[],
        title:'',
      },
    };
  }
  componentDidMount(){
    const { match:{params} } = this.props;
    fetch(`/json/${params.url}`).then((response)=>{
      return response.json();
    }).then((data)=>{
      this.setState({
        content:{
          list:data.content,
          title:data.name,
        }
      });
      const audio = this.audioEl;
      audio.map((item,index)=>{
        item.addEventListener('playing',(e)=>{
          if (this.state.theCurrentBroadcast){
            if (item !== this.state.theCurrentBroadcast){
              this.state.theCurrentBroadcast.pause();
            }
          }
          this.setState({
            theCurrentBroadcast:item,
          });
        });
        item.addEventListener('ended',(e)=>{

          audio[index+1] && audio[index+1].play();
        });
        return null
      })
    });
  }
  renderChildren = (data) => {
    if (data.url && data.preview){
      return <img src={data.url} alt="" style={{
        width:'100%'
      }}/>
    }
    if (data.url && data.duration){
      return <audio ref={(ref) => { this.audioEl.push(ref); }} src={data.url} controls style={{
        width:'100%'
      }}/>
    }
    if (data.preview){
      return <p>{data.text}</p>
    }
  }

  render() {
    const { content } = this.state
    return (
      <div className="App">
        <h4>
          {
            content.title
          }
        </h4>
        {
          content.list.map((item,index)=>(
            <div key={index}>
              {this.renderChildren(item)}
            </div>
          ))
        }
      </div>
    );
  }
}

export default App;
